const context = require.context('../static/musics',false,/\.mp3/);

module.exports = (() => {
  let matches;
  return context.keys().map((file = '',index) => {
    matches = file.match(/[^\/]+\.mp3$/)
    if(matches){
      return {
        name: matches[0].split(/\.mp3/)[0],
        src: `../static/musics/${matches[0]}`,
        active: index === 0
      };
    }
  }).filter(m => !!m);
})();



